export const url_validation = {
  name: "url",
  type: "url",
  id: "url-input",
  placeholder: "  ",
  className: "popup__input_type_url",
  validation: {
    required: {
      value: true,
      message: "    ",
    },
    pattern: {
      value:
        /(ftp|http|https):\/\/(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/,
      message: "  URL",
    },
  },
};

